* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 15px;
}
.active {
  color: #8e0537;
}
img {
  width: 32.5rem;
  height: 26rem;
}

.portrait {
  max-width: 350px;
  max-height: 250px;
  float: left;
}

.nav {
  width: 100%;
  margin: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  background-color: rgb(243, 228, 227);
  padding: 0 2rem 0.5rem 2rem;
}

.nav h1 {
  font-size: 5rem;
  text-align: center;
  margin-top: 10px;
}

.nav h2 {
  font-size: 3rem;
  text-align: center;
}

.links {
  margin-right: 10px;
  text-transform: uppercase;
}

main {
  position: relative;
  top: 14rem;
}

a {
  text-decoration: none;
  color: black;
  font-size: 2rem;
  font-weight: 100;
}

a:hover {
  color: #8e0537;
}

p {
  font-size: 1rem;
  padding: 1rem 2rem;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}

.navBottom {
  display: flex;
  justify-content: space-between;
  margin: 2rem 2rem 0 2rem;
}

.icon {
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 0.5rem;
}

.icon:hover {
  height: 2rem;
  width: 2rem;
}

.projectLinks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.projectContainer {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.imageAndHover {
  position: relative;
}

.projectImage {
  border-radius: 1rem;
}

.hoverDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(97, 93, 93);
  width: 32.5rem;
  border-radius: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  padding-left: 2rem;
  padding-right: 2rem;
  height: 26rem;
}

.hoverDiv p {
  font-size: 2rem;
  color: white;
  font-weight: 500;
}

.imageAndHover:hover .hoverDiv {
  opacity: 0.8;
}

.about {
  width: 80%;
  margin: 2rem auto;
}

.about p {
  font-size: 1.5rem;
  font-weight: 150;
  word-wrap: break-word;
}

.projectsContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

.welcomeBanner {
  background-color: rgb(243, 228, 227);
  margin-top: 0;
  padding: 4rem 2rem;
}

.welcomeBanner p {
  font-size: 2rem;
  font-weight: 100;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 750px) {
  main {
    top: 20rem;
  }
  .navBottom {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 7rem;
    padding-bottom: 0;
  }
  .welcomeBanner {
    padding-top: 5rem;
  }
  .nav {
    padding-bottom: 0;
  }
  a.links {
    font-size: 1.5rem;
  }
}
@media only screen and (max-width: 600px) {
  main {
    top: 15rem;
  }
  img {
    width: 20rem;
    height: 15rem;
  }
  .hoverDiv {
    width: 20rem;
    height: 5rem;
    opacity: 0.9;
    padding: 0.5rem;
    border-radius: 0 0 1rem 1rem;
    top: 10rem;
  }
  .hoverDiv p {
    font-size: 1.2rem;
    padding: 0.1rem;
    font-weight: 150;
  }
  .hoverDiv:hover {
    height: 5rem;
  }
  .nav h1 {
    font-size: 3rem;
  }

  .nav h2 {
    font-size: 1.5rem;
  }

  a,
  .welcomeBanner p {
    font-size: 1.2rem;
  }
  .welcomeBanner p {
    padding: 0.5rem;
  }
  .welcomeBanner {
    padding: 3.5rem 0.8rem 0.8rem;
  }
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .about p {
    font-size: 1rem;
    padding: 1rem;
  }
  .portrait {
    float: none;
    margin: 2rem auto 1rem auto;
  }
  .contact {
    margin-top: 8rem;
  }
  .projectsContainer {
    flex-direction: column;
  }
}

@media only screen and (max-width: 400px) {
  .navBottom {
    margin-top: -0.5rem;
  }
}

@media only screen and (width: 375px) {
  main {
    top: 12rem;
  }
}
